body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/mev-portfolio/static/media/banner-bg.5f3ca497.png);
  background-size: cover;
  color: rgb(211, 213, 120);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;

}
.about-me {
    text-align: left;
    display: flex;
    margin: 10px 220px;
    grid-gap: 120px;
    gap: 120px;
}

.about-me-text {
    background-color: rgba(116, 58, 163, 0.2);
    box-shadow: 0 5px 20px rgba(214, 40, 230, 0.1);
    border-radius: 20px;
    height: 400px;
    width: 700px;
    padding: 10px 10px;
    white-space: pre-wrap;
}

.about-me-image {
    margin-right: 120px;
}

.slide-in-from-left {
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
    -webkit-transform: translateX(-143%);
            transform: translateX(-143%);
}

.slide-in-from-right {
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
    -webkit-transform: translateX(200%);
            transform: translateX(200%);
}

.visible {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

.about-me h1 {
    font-size: 24px;
}

.about-me p {
    font-size: 16px;
    line-height: 1.5;
    white-space: break-spaces;
}

.about-me-image {
    width: 400px;
    height: 400px;
    margin-top: 100px;
}
.header-container {
    height: 60px;
    display: flex;
    margin: 0px 180px;
    align-items: center;
    padding-top: 10px;
}

.nav-bar button {
    background-color: rgb(109, 87, 199);
    border: rgb(251, 250, 255);
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
    margin-top: 25px;
    width: 132px;
    height: 50px;
    color: rgb(254, 254, 254);
    font-weight: bold;
}

.nav-bar button:hover {
    background-color: rgb(148, 76, 184);
}

.logo {
    flex: 1 1;
    font-size: 15px;
    text-align: left;
    padding: 0 40px;
}

.nav-bar {
    padding: 0 14%;
    display: flex;
}

.nav-bar ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 40px;
    grid-gap: 10px;
    gap: 10px;
}

.nav-bar li {
    justify-content: space-between;
}

.nav-bar a {
    text-decoration: none;
    color: rgb(211, 213, 120);
}

.nav-bar a:hover {
    color: rgb(109, 87, 199);
}
.projects {
    justify-content: center;
    margin: 0px 220px;
}

.card-container {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 375px;
    height: 250px;
    background-color: rgba(116, 58, 163, 0.6);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    cursor: pointer;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    padding: 10px 10px;
    white-space: pre-wrap;

}

.card:hover {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
}

.card__title {
    font-size: 15px;
    font-weight: bold;
    text-align: center;
    margin: 16px 0;
}

.card__description {
    font-size: 16px;
    margin: 0 16px;
    padding-bottom: 60px;
    text-align: center;
    color: #fff;
}

.card__tools {
    width: 350px;
    font-size: 13px;
    text-align: left;
    margin-bottom: 5px;
    color: rgb(225, 206, 241);
}

.card__tools li {
    margin: 0 15px;
}

.card__tools ul {
    padding-left: 15px;
    text-align: center;
    display: flex;
    list-style: none;
}
.copyright {
    font-size: 12px;
    font-weight: bold;
    text-align: left;
}

.social-buttons {
    text-align: right;
}

.social-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
}

.container {
    margin: 0 220px;
    /* justify-content: space-between; */
    align-items: flex-end;
    display: flex;
    padding-bottom: 10px;
    grid-gap: 60%;
    gap: 60%;
}
.contact {
    margin: 80px 220px;
}

.contact-container {
    display: flex;
    grid-gap: 20%;
    gap: 20%;
}

.submit-response-container {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.submit-response-container p {
    margin-top: 25px;
    font-size: 16px;
}

.contact-form {
    padding: 20px;
    background-color: rgba(116, 58, 163, 0.6);
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    width: 30%;
}

.form-input {
    margin-left: 5px;
}

.contact-image {
    margin-top: 30px;
}

.form-input input {
    justify-content: space-between;
    display: block;
    margin-top: 10px;
    width: 80%;
    box-sizing: border-box;
    border: 2px solid #6c63ff;
    padding: 0.8rem 1rem;
    border-radius: 7px;
    margin-bottom: 0.5rem;
    transition: 0.3s;
}

.form-input textarea {
    display: block;
    width: 100%;
    height: 140px;
    resize: none;
    margin-top: 10px;
    box-sizing: border-box;
    border: 2px solid #6c63ff;
    padding: 0.8rem 1rem;
    border-radius: 7px;
    margin-bottom: 0.5rem;
    transition: 0.3s;
}

.button-response-container {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    margin-top: 10px;
    height: 50px;
}

.button-container button {
    cursor: pointer;
    border-radius: 5px;
    padding: 0.5rem 1.7rem;
    border: none;
    -webkit-user-select: none;
            user-select: none;
    color: #fff;
    background: #0074d9;
    height: 40px;
}

.contact-container button:hover {
    background: #0063b1;
}
